import { Router } from '@angular/router';
import { AuthService } from './../auth.service';
import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-regisztracio',
    templateUrl: './regisztracio.component.html',
    styleUrls: ['./regisztracio.component.scss']
})
export class RegisztracioComponent implements OnInit {

    public jelszo: string = null;
    public email: string = null;
    public regUzi: string = null;

    constructor(private authService: AuthService, private router: Router) { }

    ngOnInit() {
    }

    semmi() { }

    vissza() {
        this.router.navigate(['/login']);
    }

    regisztracio() {
        console.log('regisztracio: ', this.email, this.jelszo);
        // const result: any = this.authService.signupUser(this.email, this.jelszo).;
        this.authService.regisztracio(this.email, this.jelszo).then(
            result => {
                console.log(result);
                console.log('Sikeres regisztráció');
                this.regUzi = null;
                this.router.navigate(['/login']);
            }
        ).catch(
            error => {
                console.error('regisztraciós hiba nyers:', error);
                if (error && error.message) {
                    this.regUzi = error.message;
                } else {
                    this.regUzi = 'Ismeretlen hiba...';
                }
                console.error('regisztraciós hiba:', this.regUzi);
            }
        );
    }

}

