import { Router } from '@angular/router';
import { AuthService } from './../auth.service';
import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

    public jelszo: string = null;
    public email: string = null;
    public loginHiba: string = null;

    constructor(private authService: AuthService, private router: Router) { }

    ngOnInit() {
    }

    semmi() { }

    login() {
        console.log('Indul a login');
        this.authService.belepes(this.email, this.jelszo)
            .then(
                response => {
                    console.log('Sikeres bejelentkezé. ', response);
                    this.loginHiba = null;
                    this.authService.getTokenPromise().then(
                        (token: string) => {
                            this.authService.setToken(token, this.email);
                            this.router.navigate(['/listaLista']);
                        }
                    );
                }
            )
            .catch(
                error => {
                    console.error('Hiba a bejelentkezés során! ', error);
                    if (error && error.message) {
                        this.loginHiba = error.message;
                    } else {
                        this.loginHiba = 'Ismeretlen hiba...';
                    }
                }
            );
    }

    regisztracio() {
        console.log('Irány a regisztráció');
        this.router.navigate(['/reg']);
    }

}
