import { AuthGuardService } from './auth-guard.service';
import { LoginComponent } from './login/login.component';
import { ListaValasztoComponent } from './lista-valaszto/lista-valaszto.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { RegisztracioComponent } from './regisztracio/regisztracio.component';

const routes: Routes = [
    { path: 'listaLista', component: ListaValasztoComponent, canActivate: [AuthGuardService] },
    { path: 'reg', component: RegisztracioComponent },
    { path: 'login', component: LoginComponent },
    { path: '', redirectTo: 'listaLista', pathMatch: 'full' },
    { path: '**', redirectTo: 'listaLista' }

];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
